@charset "UTF-8";

body,
div,
dl,
dt,
dd,
ul,
li,
h1,
h2,
h3,
h4,
h5,
h6,
pre,
code,
form,
fieldset,
input,
textarea,
p,
blockquote,
th,
td {
  margin: 0;
  padding: 0;
}

table {
  border-collapse: collapse;
  border-spacing: 0;
}

fieldset,
img,
abbr {
  border: 0;
}

address,
caption,
cite,
code,
dfn,
em,
strong,
th,
var {
  font-style: normal;
  font-weight: normal;
}

ul li {
  list-style: none;
}

caption,
th {
  text-align: left;
}

h1,
h2,
h3,
h4,
h5,
h6 {
  font-size: 100%;
  font-weight: normal;
}

sup {
  vertical-align: text-top;
}

sub {
  vertical-align: text-bottom;
}

input,
textarea,
select {
  font-family: inherit;
  font-size: inherit;
  font-weight: inherit;
}

legend {
  color: #000;
}

article,
aside,
details,
figcaption,
figure,
footer,
header,
hgroup,
menu,
nav,
section,
main {
  display: block;
}

img {
  max-width: 100%;
  height: auto;
}

.wrapper {
  max-width: 1280px;
  margin: 0 auto;
  padding-left: 30px;
  padding-right: 30px;
}

/* .debug {
    @include debug(rgba(0, 0, 0, 0.5), #ff0);
} */

.humans {
  background: #6cc372;
  padding: 10px 0;
}

.humans h2 {
  color: #232324;
}

.humans .h2sub {
  color: #fff;
}

.humans .items {
  display: flex;
  flex-wrap: wrap;
  margin-left: -1%;
  margin-right: -1%;
}

.humans .items .item {
  box-sizing: border-box;
  word-wrap: break-word;
  margin-left: 1%;
  margin-right: 1%;
  width: 23%;
  border-radius: 10px;
  margin-top: 15px;
  margin-bottom: 15px;
  padding: 10px;
  text-align: center;
  background: #fff;
  /*для первой картинки приходится использовать flex вручную - тут smartgrid не поможет...*/
  display: flex;
  flex-direction: column;
}

.humans .items .item img {
  margin: 10px auto;
  width: 90%;
}

.humans .items .item .subtitle {
  color: #6cc372;
  font-size: 24px;
  margin: 10px;
}

.humans .items .item .job {
  /*прописываем отступы чтобы выровнить кнопку у первой картинки */
  margin: auto 10px 10px 10px;
}

.humans .items .item .follow {
  background: #6cc372;
  color: #fff;
  font-size: 24px;
  cursor: pointer;
  border-radius: 5px;
  padding: 5px;
}

@media screen and (max-width: 992px) {
  .wrapper {
    padding-left: 15px;
    padding-right: 15px;
  }

  .humans .items .item {
    width: 48%;
  }
}

@media screen and (max-width: 576px) {
  .humans .items {
    justify-content: center;
  }

  .humans .items .item {
    width: 81.33333%;
  }
}